import React, {Component} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faLaptopCode} from '@fortawesome/free-solid-svg-icons';
import projects from '../data/projects.json';

class Portfolio extends Component {
  constructor (props) {
    super (props);
    this.state = {
      selection: 0,
    };
    this.allSkills = [
      'All',
      'HTML/CSS',
      'React.JS',
      'JavaScript',
      'Java',
      'Spring Boot',
      'Java Persistence API',
      'CRUD',
      'REST API',
      'SASS',
      'Jasmine Unit Testing',
      'Bootstrap',
      'Material UI',
      'Thymeleaf',
      'Gulp',
      'Grunt',
      'AJAX',
      'Responsive Layout',
      'Offline-first Application',
      'MVC',
      'Server Side Rendering',
      'Single Page Application',
      'Chrome Extension',
      'Browser-based Game',
    ];
  }

  handleSelect = index => {
    this.setState ({selection: index});
  };
  render () {
    return (
      <div className="portfolio">
        <main>
          {/* <h1>FRONT END WORK</h1> */
          console.log (projects)}
          <FontAwesomeIcon icon={faLaptopCode} className="section-logo" />

          <div className="filterSkills">
            {this.allSkills.map ((skill, index) => (
              <button
                className={`filterButton ${this.state.selection === index && 'selected'}`}
                key={index}
                onClick={() => this.handleSelect (index)}
              >
                {skill}
              </button>
            ))}
          </div>

          {this.state.selection === 0
            ? <p className="selected-skill">
                Showing
                {' '}
                <span>all</span>
                {' '}
                projects. Click a skill or technology to filter
              </p>
            : <p className="selected-skill">
                Showing projects filtered by
                {' '}
                <span>{this.allSkills[this.state.selection]}</span>
                .
              </p>}

          <div className="container">
            {projects.map (
              project =>
                project.tags.includes (this.allSkills[this.state.selection]) &&
                <div className="flex-project" key={project.title}>
                  <a
                    href={project.demoUrl}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <picture>
                      <source
                        media="(min-width: 650px)"
                        srcSet={project.medImage}
                      />
                      <img
                        className="proj-img"
                        src={project.smallImage}
                        alt={`${project.title} Screenshot`}
                      />
                    </picture>
                  </a>
                  <h2 className="proj-title">{project.title}</h2>
                  <p className="proj-links">
                    {project.demoUrl &&
                      <a
                        href={project.demoUrl}
                        rel="noopener noreferrer"
                        target="_blank"
                      >
                        DEMO
                      </a>}
                    {project.demoUrl && <span> | </span>}
                    <a
                      href={project.codeUrl}
                      rel="noopener noreferrer"
                      target="_blank"
                    >
                      CODE
                    </a>
                  </p>
                  <p>{project.description}</p>
                </div>
            )}
          </div>
        </main>
      </div>
    );
  }
}

export default Portfolio;
